.footer {
  padding-top: 69px;
  transition-duration: .25s;
}

.footer__container {
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  row-gap: 20px;
  height: 107px;
  display: flex;

  @media screen and (width >= 768px) {
    justify-content: space-between;
  }
}

.footer__logo {
  flex-shrink: 0;
  width: 149px;

  @media screen and (width >= 768px) {
    width: 225px;
  }
}

.footer__list-pages {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.footer__page {
  color: #111;
  letter-spacing: .16px;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  transition-duration: .25s;
}

.footer__page:hover {
  color: #a1d737;
  transition-duration: .25s;
}

.footer__social-list {
  gap: 32px;
  display: flex;
}

.footer__social {
  cursor: pointer;
  border: 1px solid #11111126;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  width: 56px;
  height: 56px;
  transition-duration: .25s;
  display: flex;
}

.footer__social:hover {
  border-color: #a1d737;
  transition-duration: .25s;
}

.footer__social:hover svg {
  fill: #a1d737;
  transition-duration: .25s;
}

.footer__social-logo {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
}

.footer__privacy {
  color: #1116;
  text-align: center;
  margin-top: 128px;
  padding-bottom: 16px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.footer__info-section {
  display: flex;
}

#night {
  background: #0d171d;
}

#night-page {
  color: #fafafa;
  transition-duration: .25s;
}

#night-page:hover {
  color: #a1d737;
  transition-duration: .25s;
}

#night-social {
  border-color: #fafafa26;
}

#night-social:hover {
  border-color: #a1d737;
  transition-duration: .25s;
}

#night-social:hover svg {
  fill: #a1d737;
  transition-duration: .25s;
}

#night-social-picture {
  fill: #fafafa;
}

#night-privacy {
  color: #fafafa66;
}
